import { useDispatch } from 'react-redux';
import { Button, Grid, Stack, TextField, Select } from '@material-ui/core';
import { Formik, FormikHelpers } from 'formik';
import * as yup from 'yup';
import { useQuery, gql } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { generatePath, useHistory } from 'react-router-dom';
import MainCard from '../../../ui-component/cards/MainCard';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { SNACKBAR_OPEN } from '../../../store/actions';
import SurveyComponent from './components/SurveyCreator';
import Loader from '../../../ui-component/Loader';
import { listFormCategories } from '../../../graphql/queries';
import { createForm } from '../../../graphql/mutations';
import { gridSpacing } from '../../../store/constant';
import { client } from '../../../dataProvider';
import InputLabel from '../../../ui-component/extended/Form/InputLabel';
import { FormType } from '../../../types';

const FormAdd = () => {
	const dispatch = useDispatch();
	const history = useHistory();

	function saveSurveyFunc() {
		// @ts-ignore
		// eslint-disable-next-line react/no-this-in-sfc
		sessionStorage.setItem('template', JSON.stringify(this.JSON));
	}

	const {
		loading: isFormTypeLoading,
		error: isFormTypeError,
		data: formCategories,
	} = useQuery(
		gql`
			${listFormCategories}
		`
	);

	if (isFormTypeLoading) return <Loader />;
	if (isFormTypeError) {
		dispatch({
			type: SNACKBAR_OPEN,
			open: true,
			message: 'Something went wrong',
			variant: 'alert',
			alertSeverity: 'error',
			anchorOrigin: { vertical: 'top', horizontal: 'center' },
		});
		return <></>;
	}

	if (_isEmpty(formCategories)) return <></>;
	const formTypes = _get(formCategories, 'listFormCategories');

	const onSubmitHandler = async (
		values: FormType,
		{ setStatus, setSubmitting }: FormikHelpers<FormType>
	) => {
		const { formTypeId: categoryAndFormType, title } = values;
		const [categoryId, formTypeId] = categoryAndFormType.split(',');

		const currentTemplate = sessionStorage.getItem('template') as string;

		try {
			const data = await client.mutate({
				mutation: gql`
					${createForm}
				`,
				variables: {
					Form: {
						CategoryId: categoryId,
						FormTypeId: formTypeId,
						Title: title,
						Template: currentTemplate,
					},
				},
			});
			dispatch({
				type: SNACKBAR_OPEN,
				open: true,
				message: 'Form has created',
				variant: 'alert',
				alertSeverity: 'success',
				anchorOrigin: { vertical: 'top', horizontal: 'center' },
			});
			const formId = _get(data, ['data', 'createForm', 'FormId']);
			const formNewLink = generatePath('/form/:id', {
				id: formId,
			});
			history.push(formNewLink);
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);

			dispatch({
				type: SNACKBAR_OPEN,
				open: true,
				message,
				variant: 'alert',
				alertSeverity: 'error',
				anchorOrigin: { vertical: 'top', horizontal: 'center' },
			});
		}
	};
	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<MainCard title='Add New Form'>
					<Formik
						initialValues={{
							title: '',
							formTypeId: '',
						}}
						validationSchema={yup.object().shape({
							title: yup.string().required('Title is require'),
							formTypeId: yup.string().required('Form Type is require'),
						})}
						onSubmit={onSubmitHandler}
					>
						{({
							errors,
							handleChange,
							handleBlur,
							handleSubmit,
							touched,
							values,
							isSubmitting,
						}) => (
							<form noValidate onSubmit={handleSubmit}>
								<Grid container spacing={2} alignItems='center'>
									<Grid item xs={12} lg={6}>
										<InputLabel>Title</InputLabel>
										<TextField
											defaultValue={values.title}
											fullWidth
											placeholder='Title'
											onChange={handleChange}
											onBlur={handleBlur}
											error={touched.title && Boolean(errors.title)}
											helperText={touched.title && errors.title}
											name='title'
										/>
									</Grid>
									<Grid item xs={12} lg={6}>
										<InputLabel htmlFor='formTypeId'>Form Type</InputLabel>
										<Select
											native
											id='formTypeId'
											defaultValue={values.formTypeId}
											onChange={handleChange}
											error={touched.formTypeId && Boolean(errors.formTypeId)}
											name='formTypeId'
										>
											{formTypes.map((data: any) => {
												const {
													Title: categoryName,
													FormTypes: types,
													id: categoryId,
												} = data;
												return (
													<optgroup label={categoryName} key={categoryId}>
														{types.map((formType: any) => {
															const { Name: name, id: typeId } = formType;
															const categoryIdAndFormTypeId = `${categoryId},${typeId}`;
															return (
																<option
																	value={categoryIdAndFormTypeId}
																	key={categoryIdAndFormTypeId}
																>
																	{name}
																</option>
															);
														})}
													</optgroup>
												);
											})}
										</Select>
									</Grid>
									<Grid item xs={12} lg={12}>
										<SurveyComponent
											value=''
											name='Template'
											saveSurveyFunc={saveSurveyFunc}
										/>
									</Grid>
									<Grid item xs={12}>
										<Stack direction='row' justifyContent='flex-end'>
											<AnimateButton>
												<Button
													color='primary'
													variant='contained'
													type='submit'
													disabled={isSubmitting}
												>
													Add
												</Button>
											</AnimateButton>
										</Stack>
									</Grid>
								</Grid>
							</form>
						)}
					</Formik>
				</MainCard>
			</Grid>
		</Grid>
	);
};

export default FormAdd;
