import _get from 'lodash.get';
import Inputmask from 'inputmask';
import {
	ELEMENTS_TITLE,
	ELEMENTS_TYPE,
	SURVEYJS,
	WIDGET_BASE,
} from '../Constants';
import { ELEMENTS_ID, PHONE_INPUT_MASK } from './Constants';

const inputMask = new Inputmask(PHONE_INPUT_MASK, {
	removeMaskOnSubmit: true,
	autoUnmask: true,
	prefix: '+',
});

type Question = {
	value: string;
	isReadOnly: boolean;
	readOnlyChangedCallback: () => any;
	valueChangedCallback: () => void;
	getType: () => string;
};

const onPhoneInputChangeHandler = (question: Question, input: HTMLElement) => {
	const phoneValue = _get(input, 'value', '').replace(' ', '');
	// eslint-disable-next-line no-param-reassign
	question.value = `+${phoneValue}`;
};

const onValueChangedCallback = (
	question: Question,
	input: { value: string }
) => {
	// eslint-disable-next-line no-param-reassign
	input.value = _get(question, 'value', '');
};

const onReadOnlyChangedCallback = (
	question: Question,
	input: {
		setAttribute: (attribute: string, status: string) => any;
		removeAttribute: (attribute: string) => any;
	}
) => {
	if (question.isReadOnly) return input.setAttribute('disabled', 'true');
	return input.removeAttribute('disabled');
};

const getElements = (rootElement: HTMLElement, elementTagnames: any[]) =>
	elementTagnames.reduce(
		(acc: { [x: string]: HTMLElement | null }, current: string) => {
			const element = document.getElementById(current);
			if (element) {
				// eslint-disable-next-line no-param-reassign
				acc[element.tagName.toLowerCase()] = element;
			}
			return acc;
		},
		[]
	);

const htmlTemplate = `
  <div class="PhoneInputCountry">
    <input id=${ELEMENTS_ID.phoneTextField} type="tel" autocomplete="tel" class="sv-custom-phone__input"
    pattern="[0-9]{12}" required>
  </div>
`;

export const PhoneInput = (Survey: any) => {
	const widgetType = ELEMENTS_TYPE.phoneInput;
	const widget = {
		...WIDGET_BASE(widgetType, ELEMENTS_TITLE.phoneInput),
		htmlTemplate,
		activatedByChanged: () => {
			const metaData = _get(Survey, SURVEYJS.metaData);
			metaData.addClass(widgetType, [], null, 'empty');
		},
		afterRender: (question: Question, rootElement: any) => {
			const elements = getElements(rootElement, [ELEMENTS_ID.phoneTextField]);
			const { input } = elements;

			inputMask.mask(input);
			input.addEventListener('change', () =>
				onPhoneInputChangeHandler(question, input)
			);

			// eslint-disable-next-line no-param-reassign
			question.readOnlyChangedCallback = () =>
				onReadOnlyChangedCallback(question, input);
			// eslint-disable-next-line no-param-reassign
			question.valueChangedCallback = () =>
				onValueChangedCallback(question, input);
			(function initial() {
				onValueChangedCallback(question, input);
			})();
		},
	};

	const Instance = _get(Survey, SURVEYJS.widgetCollectionInstance);
	Instance.addCustomWidget(widget, SURVEYJS.customType);
};
